<!-- src/components/MetroTimeFilter.vue -->
<template>
  <div class="metro-time-filter">
    <form @submit.prevent="fetchSortedFlats">
      <div class="form-group">
        <label for="by-transport">Выберите тип транспорта:</label>
        <select id="by-transport" v-model="byTransport" required>
          <option :value="true">На транспорте</option>
          <option :value="false">Пешком</option>
        </select>
      </div>

      <div class="form-group">
        <label for="desc">Выберите порядок сортировки:</label>
        <select id="desc" v-model="desc" required>
          <option :value="true">По убыванию</option>
          <option :value="false">По возрастанию</option>
        </select>
      </div>

      <button type="submit" class="submit-button">Получить квартиры</button>
    </form>

    <div v-if="apartments && apartments.length" class="apartments-list">
      <h3>Квартиры, отсортированные по времени до метро:</h3>
      <div v-for="apt in apartments" :key="apt.id">
        <table>
          <tbody>
          <tr><td><strong>ID:</strong></td><td>{{ apt.id }}</td></tr>
          <tr><td><strong>Название:</strong></td><td>{{ apt.name }}</td></tr>
          <tr><td><strong>Время до метро:</strong></td><td>{{ byTransport ? apt.timeToMetroByTransport : apt.timeToMetroByFoot }}</td></tr>
          </tbody>
        </table>
      </div>
    </div>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
import apiClient from '../apiClient';

export default {
  data() {
    return {
      byTransport: false,   // true for transport, false for walking
      desc: false,          // true for descending, false for ascending
      apartments: [],
      errorMessage: ''
    };
  },
  methods: {
    async fetchSortedFlats() {
      this.errorMessage = '';
      this.apartments = [];
      try {
        const response = await apiClient.get(`/agency/get-ordered-by-time-to-metro/${this.byTransport}/${this.desc}`, {
          headers: { 'Content-Type': 'application/xml' }
        });

        if (response.status === 200) {
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(response.data, 'application/xml');
          const items = xmlDoc.getElementsByTagName("item");

          // Parse each item and add it to the apartments array
          this.apartments = Array.from(items).map(item => this.parseApartmentXML(item));
        }
      } catch (error) {
        if (error.response && error.response.data) {
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(error.response.data, 'application/xml');
          const messageNode = xmlDoc.querySelector('message');
          this.errorMessage = messageNode ? messageNode.textContent : 'Не поступило ошибки от сервера';
        } else {
          this.errorMessage = 'Сервер не работает';
        }
      }
    },

    parseApartmentXML(item) {
      return {
        id: item.getElementsByTagName("id")[0].textContent,
        name: item.getElementsByTagName("name")[0].textContent,
        timeToMetroByFoot: item.getElementsByTagName("timeToMetroByFoot")[0].textContent,
        timeToMetroByTransport: item.getElementsByTagName("timeToMetroByTransport")[0].textContent
      };
    }
  }
}
</script>

<style scoped>
.metro-time-filter {
  max-width: 600px;
  margin: 20px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

select, button {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
}

.submit-button {
  background: linear-gradient(135deg, #25d6d6, #2b18a3);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin-top: 15px;
}

.apartments-list {
  margin-top: 20px;
  padding: 15px;
  color: #333;
  text-align: center;
  border-radius: 8px;
}

.apartment-list table {
  width: 100%;
  border-collapse: collapse;
}

.apartment-list table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.apartment-list table td strong {
  font-weight: bold;
  color: #333;
}


.error-message {
  color: #ff4d4f;
  font-weight: bold;
  margin-top: 10px;
}
</style>
