  </div>
</template>

<script>
import FindMostExpensiveFlat from "@/components/FindMostExpensiveFlat.vue";
import MetroTimeFilter from "@/components/MetroTimeFilter.vue";

export default {
  components: {
    FindMostExpensiveFlat,
    MetroTimeFilter
  },
  data() {
    return {
      activeForm: null
    };
  },
  methods: {
    toggleForm(formName) {
      this.activeForm = this.activeForm === formName ? null : formName;
    },
    closeForm() {
      this.activeForm = null;
    }
  }
};
</script>

